.App {
  text-align: center;
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;

  }
}

.App-header {
  background-image: linear-gradient(black,grey);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: white;
  font-weight: bold;
}

.App-link {
  color: white;
  text-decoration: none;
}

.App-link:hover {
  color: lime;

}

form.gform {
  background-color: black;
  width: 100%;
}

label#formLabel {
  display: block;
  width: 100%;
  font-size: 55%;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 1%;
  font-weight: bold;


}


input[type=email] {
  display: block;
  margin: auto;
  padding: 4px 15px;
  width: 40%;

}

input[type=submit] {
  display: block;
  background-color: #4CAF50;
  color: white;
  padding: 6px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: auto;
  margin-top: 1%;
  margin-bottom: 2%;
  font-weight: bold;


}

input[type=submit]:hover {
  background-color: #45a049;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
